body {
  background-color: #e6e6e6;
}
.main {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.logo {
  width: 100%;
}

.image {
  width: 100%;
}

@media all and (min-width: 768px) {
  .menu {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto 300px 3em;
  }
  .menu {
    margin-top: 20px;
  }
  .image {
    width: 400px;
  }
}
